.spaceBetween{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.spanWrap{
    margin: 5px 0;
}
.wrap{
    flex-wrap: wrap;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.box{
    /*width: 289px;*/
    border: 2px solid rebeccapurple;
    margin: 10px 0;
    padding: 10px;
}

.table{
    width: 100%;
    background: #fff;
    border-radius: 4px;
    box-shadow: 3px 2px 5px 1px #0000002b;
}
.TableHead{
    background: #3f51b5;
    color: #fff;
    border-radius: 4px 4px 0 0;
}
.TableRow{
    display: flex;
    padding: 15px 0;
    text-align: center;
    border-bottom: 1px solid #777777;
}
.TableBody{
    padding: 8px 2px 20px;
}
.large{
    width: 50%;
}
.small{
    width: 20%;
}
.medium{
    width: 30%;
}